import {
    Modal,
    ModalAction,
    ModalCancel,
    ModalContent,
    ModalDescription,
    ModalFooter,
    ModalHeader,
    ModalTitle,
} from '@/components/commons/Modal';
import React from 'react';
import { useNavigate } from 'react-router-dom';

export default function CancelRegModal({
    currentPage,
    setCancelReg,
}: {
    currentPage: string;
    setCancelReg: (state: boolean) => void;
}) {
    const navigate = useNavigate();
    return (
        <div className="fixed">
            <Modal open={true}>
                <ModalContent>
                    <ModalHeader>
                        <ModalTitle>{currentPage ? '  ' : ''}</ModalTitle>
                        <ModalDescription>
                            {currentPage
                                ? '    ?'
                                : '   ?'}
                        </ModalDescription>
                    </ModalHeader>
                    <ModalFooter>
                        <ModalCancel onClick={() => setCancelReg(false)}></ModalCancel>
                        <ModalAction onClick={() => navigate('/')}>
                            {currentPage ? '' : ''}
                        </ModalAction>
                    </ModalFooter>
                </ModalContent>
            </Modal>
        </div>
    );
}
