const RE = 6371.00877; //  (km)
const GRID = 5.0; //  (km)
const SLAT1 = 30.0; //  1(degree)
const SLAT2 = 60.0; //  2(degree)
const OLON = 126.0; //  (degree)
const OLAT = 38.0; //  (degree)
const XO = 43; //  X(GRID)
const YO = 136; // 1 Y(GRID)
const DEG_TO_RAD = Math.PI / 180.0;

const re = RE / GRID;
const slat1 = SLAT1 * DEG_TO_RAD;
const slat2 = SLAT2 * DEG_TO_RAD;
const olon = OLON * DEG_TO_RAD;
const olat = OLAT * DEG_TO_RAD;

interface Grid {
    nx: number;
    ny: number;
}
export const gpsToGrid = (lat: number, lng: number): Grid => {
    let sn = Math.tan(Math.PI * 0.25 + slat2 * 0.5) / Math.tan(Math.PI * 0.25 + slat1 * 0.5);
    sn = Math.log(Math.cos(slat1) / Math.cos(slat2)) / Math.log(sn);
    let sf = Math.tan(Math.PI * 0.25 + slat1 * 0.5);
    sf = (Math.pow(sf, sn) * Math.cos(slat1)) / sn;
    let ro = Math.tan(Math.PI * 0.25 + olat * 0.5);
    ro = (re * sf) / Math.pow(ro, sn);

    let ra = Math.tan(Math.PI * 0.25 + lat * DEG_TO_RAD * 0.5);
    ra = (re * sf) / Math.pow(ra, sn);
    let theta = lng * DEG_TO_RAD - olon;
    if (theta > Math.PI) theta -= 2.0 * Math.PI;
    if (theta < -Math.PI) theta += 2.0 * Math.PI;
    theta *= sn;
    const nx = Math.floor(ra * Math.sin(theta) + XO + 0.5);
    const ny = Math.floor(ro - ra * Math.cos(theta) + YO + 0.5);

    return { nx, ny };
};
