import React from 'react';
import {
    Modal,
    ModalAction,
    ModalCancel,
    ModalContent,
    ModalDescription,
    ModalFooter,
    ModalHeader,
    ModalTitle,
} from '@/components/commons/Modal';
import { useDog } from '@/hooks/useDog';
interface Props {
    id: number;
    name: string;
    setDeleteDogConfirm: (state: boolean) => void;
}
export default function DeleteDogModal({ id, name, setDeleteDogConfirm }: Props) {
    const { deleteDog } = useDog();
    return (
        <div className="fixed">
            <Modal open={true}>
                <ModalContent>
                    <ModalHeader>
                        <ModalTitle className="text-start"> </ModalTitle>
                        <ModalDescription className="text-start">
                            {name}()   ?
                        </ModalDescription>
                    </ModalHeader>
                    <ModalFooter>
                        <ModalCancel onClick={() => setDeleteDogConfirm(false)}></ModalCancel>
                        <ModalAction onClick={() => deleteDog.mutate(id)}></ModalAction>
                    </ModalFooter>
                </ModalContent>
            </Modal>
        </div>
    );
}
