import {
    Modal,
    ModalAction,
    ModalCancel,
    ModalContent,
    ModalDescription,
    ModalFooter,
    ModalHeader,
    ModalTitle,
} from '@/components/commons/Modal';
import { useAuth } from '@/hooks/useAuth';
import React from 'react';

export default function DeactivateModal({ setDeactivate }: { setDeactivate: (state: boolean) => void }) {
    const { deactivate } = useAuth();
    return (
        <div className="fixed">
            <Modal open={true}>
                <ModalContent>
                    <ModalHeader>
                        <ModalTitle> </ModalTitle>
                        <ModalDescription>   ?</ModalDescription>
                    </ModalHeader>
                    <ModalFooter>
                        <ModalCancel onClick={() => setDeactivate(false)}></ModalCancel>
                        <ModalAction onClick={() => deactivate.mutate(null)}></ModalAction>
                    </ModalFooter>
                </ModalContent>
            </Modal>
        </div>
    );
}
