import {
    Modal,
    ModalAction,
    ModalCancel,
    ModalContent,
    ModalDescription,
    ModalFooter,
    ModalHeader,
    ModalTitle,
} from '@/components/commons/Modal';
import { MIN_DIMENSION } from '@/constants/cropper';
import { useStore } from '@/store';
import { MutableRefObject } from 'react';

interface Props {
    fileInputRef: MutableRefObject<HTMLInputElement | null>;
}
export default function CropperModal({ fileInputRef }: Props) {
    const setCrop = useStore((state) => state.setCrop);
    const setCropError = useStore((state) => state.setCropError);
    const handleChange = () => {
        fileInputRef.current?.click();
        setCropError(false);
    };
    return (
        <div className="fixed">
            <Modal open={true}>
                <ModalContent>
                    <ModalHeader>
                        <ModalTitle>  </ModalTitle>
                        <ModalDescription>
                            {MIN_DIMENSION} X {MIN_DIMENSION}   .
                        </ModalDescription>
                    </ModalHeader>
                    <ModalFooter>
                        <ModalCancel
                            onClick={() => {
                                setCropError(false);
                                setCrop(undefined);
                            }}
                        >
                            
                        </ModalCancel>
                        <ModalAction onClick={handleChange}> </ModalAction>
                    </ModalFooter>
                </ModalContent>
            </Modal>
        </div>
    );
}
